import {uniIcon} from "@dcloudio/uni-ui"
export default {
	components:{
		uniIcon
	},
	data() {
		return {
			pageSize : 12,//一次搜索数量
			value:"",//搜索内容
			result:[],//搜索结果
			show:false,//显示搜索结果
		}
	},
	onLoad() {
	},
	onShow() {
		// this.value='';
		// this.show = false;
	},
	onReachBottom(){
		this.pageSize+=12;
		console.log(this.pageSize);
		this.search();
	},
	methods: {
		toMovieDetail(id){
			uni.navigateTo({
				url: '../../pages/detail/detail?id='+id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		search(){//搜索
			uni.request({
				url: `${this.$url}/search/list?page=1&&pageSize=`+this.pageSize+`&&keywords=`+this.value+`&&qq=40699904`,
				method: 'POST',
				data: {},
				success: res => {
					console.log(res.data.data);
					this.result = res.data.data.rows
					console.log(this.result);
					this.show = true;
				},
				fail: () => {},
				complete: () => {}
			});
		},
	}
}
